import { StatusBar } from 'expo-status-bar';
import React from 'react';
import { View, Image, TouchableOpacity, Dimensions } from 'react-native';
import { Audio } from 'expo-av';

const {height, width} = Dimensions.get('window');

var pic1 = require('./assets/pics/e1.jpeg');
var pic2 = require('./assets/pics/d1.jpg');
var pic3 = require('./assets/pics/t1.jpg');

var sound1 = require('./assets/sounds/e1.mp3');
var sound2 = require('./assets/sounds/d1.mp3');
var sound3 = require('./assets/sounds/t1.mp3');

var pics = [pic1, pic2, pic3]
var sounds = [sound1, sound2, sound3]

var idx = 0;

const App = () => {
  const [sound, setSound] = React.useState();

  async function playSound() {
    console.log('Loading Sound');
    var { sound } = await Audio.Sound.createAsync(sounds[idx]);

    setSound(sounds[idx]);

    console.log('Playing Sound');
    await sound.playAsync();
  }

  async function handleClick() {
    idx= (idx + 1) % 3;
    playSound()
  }

  // TODO
  // React.useEffect(() => {
  //   return sound
  //     ? () => {
  //         console.log('Unloading Sound');
  //         sound.unloadAsync(); }
  //     : undefined;
  // }, [sound]);

  return (
    <View >
      <StatusBar style="auto" />
      <TouchableOpacity onPress={handleClick} >
        <Image
          source={pics[idx]}
          style={{ width: width, height: height }}
        />
      </TouchableOpacity>
    </View>
  )
}

export default App